@import url(common.scss);
//$h1-font:'S-CoreDream-3Light';
$content-font: 'SUITE-Regular';
$content-main-font:'SBAggroB';

@media screen and (min-width: 1365px) {
  html{
    font-size: 19px;
   // background: red;
  }
}
@media screen and (max-width: 1366px) and (min-width: 1024px) {
  body{
    background: #ff5e00;
    // 높이: 768최대
  }
}
@media screen and (max-width: 1023px) and (min-width: 768px) {
  body{
    background: yellow;
  }
}
@media screen and (max-width: 765px) and (min-width: 500px){
  body{
    background: blue;
  }
}


.container{
  font-family: $content-font;
  //background:linear-gradient(180deg, rgba(254,254,254,1) 43%, rgba(220,219,218,1) 100%);
background-size: cover;
background-attachment: fixed;
width: 100%;
height: 10000px;
overflow-x: hidden;



  // section1
// 소개
.content1{
  position: relative;top: 0;left: 0;;
  width: 100%;height: 1300px;
  //background: #ffebeb;
  font-family: $content-font;

.content1_groub{
  display: flex;
  position: relative;top: 50%;left: 50%;
  width: 85%;height: 85vh;
  transform: translate(-50%, -50%);
  background: #ffffffcc;
  .inform_box1{
    position: relative;top: 0;left: 0;
    width: 50%;
    //border: 1px solid red;
    .inform1{
     position: absolute;top: 8%;left: 50%;
     transform: translateX(-50%);
     width: 350px;height: 320px;
     border-radius: 700% 500% 400% 500%;
     background: #FFF340;
    }
   /*  .inform2{
      position: absolute;top: 55%;right: 20%;
      //transform: translateX(-50%);
      //text-align: center;
      font-size: 18px;
    } */
    .inform3{
      position: absolute;top:60%;left:50%;
      transform: translateX(-50%);
      ul{
        li{
          font-size: 1.0rem;
          line-height: 2.5rem;
          span{
            display: inline-block;
            padding-right: 1rem;
            transform: translateY(3px);
            }
        }
      }
    }
   }

   .inform_box2{
    position: relative;top: 0;right: 0;
    width: 50%;
    //background: rgb(97, 97, 31);
    .inform4{
      position: absolute;top: 0%;left: 0%;
      width: 90%;
      .p_groub1{
        p{
          &:nth-of-type(1){
          font-family: $content-main-font;
          -webkit-text-stroke: 2px #000;
          color: transparent;
          font-size: 3.5rem;
          }
          &:nth-of-type(2){
          font-family: $content-main-font;
          font-size: 3.5rem;
          
          }
      }
      }
      .p_groub2{
        p{
          font-size: 1rem;
          color: #252525;
        }
      }
      
     }

    .inform5{
      position: absolute;top: 45%;left: 0%;
      width: 90%;
      .intd{
        font-size: 1.7rem;
        font-family: $content-main-font;
        padding-bottom: 0.7rem;
        &::after{
          z-index: -1;
          position: absolute;top:8px;left: -5px;
          content: "";display: inline-block;
          width: 11.2rem;height: 0.8rem;
          background: #FFF340;
        }
      }
      ul{
        li{
          line-height: 1.5rem;
          font-size: 1rem;
          color: #252525;
        }
      }
    }

    .inform6{
      position: absolute;top: 72%;left: 0%;
      width: 80%;
      .intd{
        font-size: 1.7rem;
        font-family: $content-main-font;
        padding-bottom: 0.7rem;
        &::after{
          z-index: -1;
          position: absolute;top:8px;left: -5px;
          content: "";display: inline-block;
          width: 5.1rem;height: 0.8rem;
          background: #FFF340;
        }
      }
     ul{
      display: flex;
      flex-wrap: wrap;
      width: 350px;
      li{
         padding: 0 8px;
         svg{
          width: 28px;
         }
      }
     }
    }
   }
}

.inform_box3{
  .one1{
    position: absolute;bottom: 15%;right: -10%;
    width: 600px;height: 600px;
    border-radius: 50%;
    filter: blur(100px);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255, 243, 80, 0.415) 100%);
  }
  .one2{
    position: absolute;top: 0%;left: 0;
    width: 600px;height: 600px;
    border-radius: 50%;
    filter: blur(100px);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255, 243, 80, 0.415) 100%);
  }
}



}
  
